.nav {
    display: flex;
    justify-content: space-between;
    height: 80px;
    width: 100vw;
    padding: 24px 32px 24px 40px;
    color: $text-grey;   
}

.nav-left {
    display: flex;
    align-items: center;
    a {
        display: flex;
        align-items: center;
    }
    img {
        height: 24px;
    }
}

.nav-right {
    display: flex;
    text-transform: uppercase;

    a {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 16px;
        font-size: 16px;
    }
}

.active-link {
    font-weight: 700;
    text-underline-offset: 6px;
    color: #0A9BA5;
}

#navbar {
    background-color: rgba(249,249,249,50%);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    opacity: 1;
    transition: opacity 0.3s ease-in-out;
    backdrop-filter: blur(8px);
    }
    
    #navbar.show {
        opacity: 1;
        transition: opacity 0.3s ease-in-out;
    }
    
    #navbar.hidden {
        opacity: 0;
      }