@import "./variables.scss";
@import "./components/mixins.scss";
@import "./components/base.scss";
@import "./components/buttons.scss";
@import "./components/animations.scss";
@import "./components/nav.scss";
@import "./components/footer.scss";

* {
    font-family: $text-sans;
}

body {
    background-color: #ffffff;
  }

#content {
    margin-top: 80px;
}

H1 {
    font: {
        size: 2.5rem;
        weight: 400;
    }
}

.subtitle {
    font: {
        size: 1.5rem;
        weight: 300;
    }
    color: #6a6a6a;
}

H2 {
    font: {
        size: 1.625rem;
        weight: 500;
    }
}

.h2 {
    font: {
        size: 1.625rem; 
        weight: 600;
    }
    padding-bottom: 20px;
}

H3 {
    font: {
        size: 1.375rem;
        weight: 400;
    }
    color: $text-color;
}

H4 {
    font: {
        size: 1.125rem;
        weight: 600;
    }
    color: $text-color;
}

.highlight {
    font-weight: 600;
}

.h4-handwrite {
    font: {
        size: 1.25rem;
        family: $text-hand, sans-serif;
    }
}

.handwriting {
    font-family: $text-hand;
}

.green {
    color: $primary-color;
}

.white {
    color: #ffffff;
}

.bg-transparent {
    background-color: rgba(0,0,0,0%) !important;
}

.underline {
    text-decoration: underline;
    text-underline-offset: 6px;
}

/* Project Layout */

.cover-container {
    height: 560px;
    width: 100vw;
    overflow: hidden;
}

.cover {
    height: 100%;
    width: 100vw;
    object-fit: cover;
    object-position: 0;
}

.project-intro {
    display: flex;
    justify-content: center;
    align-items: start;
    padding: 80px 0;
    gap: 5%;
    color: #ffffff;
}

.intro-title {
    font-weight: 600;
    text-transform: uppercase;
    padding-bottom: 4px;
}

.intro-content {
    font-weight: 400;
    line-height: 1.5;
}

.middle {
    display: flex;
    justify-content: center;
}

.padding-end {
    padding-bottom: 36px;
}

.project-text {
    color: $text-color;
    width: 720px;
    line-height: 1.5;
    font-size: 1.125rem;
    img {
        width: 720px;
        height: auto;
        margin: 24px 0;
    }
    h2 {
        font-size: 1.625rem;
        padding: 64px 0 20px 0;
        font-weight: 600;
    }
    h4 {
        font-weight: 400;
        padding: 24px 0 12px 0;
    }
    ul, ol {
        padding: 24px 0 24px 36px;
        li {
            line-height: 1.5;
        }
    }
}

.project-3grid {
    width: 840px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 28px;
    padding: 36px 0 36px 0;
}

.threegrid-box {
    padding: 32px;
    border-radius: 16px;
    p {
        line-height: 1.5;
    }
}

.icon-title {
    display: flex;
    align-items: center;
    padding-bottom: 12px;
    img {
        padding-right: 8px;
    }
    
}

.project-quote {
    display: flex;
    width: 840px;
    border-radius: 16px;
    padding: 40px 36px;
    margin: 36px 12px 0 12px;
    p {
        font-size: 1.25rem;
        color: $text-color;
        line-height: 150%;
        padding: 0 24px;
    }
}

.quote-left {
    padding-bottom: 60px;
}
.quote-right {
    padding-top: 60px;
}

.project-fullimg {
    background-color: $project-bg-color;
    margin: 36px 0;
    padding: 32px 0;
    display: flex;
    justify-content: center;
    img {
        width: 960px;
    }
}

.project-lgimg {

    display: flex;
    justify-content: center;
    margin: 16px 0;
    border-radius: 20px;
    img {
        width: 840px;
        padding: 28px;

    }
}

.project-medimg {
    display: flex;
    justify-content: center;
    padding: 36px 0;
}

.numbered-features {
    margin-top: 36px;
}

.big-numbers {
    font-size: 2.25rem;
    font-weight: 800;
}

.big-descriptions {
    font-size: 1.25rem;
    font-weight: 600;
}

.number-onleft {
    display: flex;
    gap: 16px;
    align-items: center;
}

.onleft-title {
    font-size: 1.25rem;
    font-weight: 600;
}

.feature-section {
    margin-bottom: 48px;
}

.img-cap {
    img {
    margin-bottom: 4px !important;
    }
    margin-bottom: 24px;
}

.caption {
    display: flex;
    justify-content: center;
    font-size: 1rem;
    font-style: italic;

}

.thankyou {
    display: flex;
    height: 90px;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    padding-top: 24px;
}

.summary {
    padding: 48px;
    border-radius: 16px;
}

.block {
    margin: 16px 0;
    padding: 32px;
    border-radius: 16px;
}

.feature {
    width: 720px;
    padding: 48px 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 32px;
    video {
        height: 480px;
        border-radius: 44px;
    }
}

.feature-text-box {
    display: flex;
    align-items: center;
}

.feature-video-box {
    display: flex;
    justify-content: center;
    align-items: center;
}


/* spacing modifiers */
.fourpx {
    margin-bottom: 4px;
}

.twelvepx {
    margin-bottom: 12px;
}

.twelvepxtp {
    margin-top: 12px;
}

.twentyfourtp {
    margin-top: 24px;
}

.zeropd {
    margin-top: 0;
    padding: 0;
}


/* responsive */

@media (max-width: 720px) {
    h1 {
        font-size: 2.25rem;
    }
}